<template>
  <div class="container">
    <section>
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item
          ></el-breadcrumb-item
        >
      </el-breadcrumb>
    </section>
    <section class="shop-info">
      <span class="clearfix" style="float: right;color: #409eff;cursor: pointer;margin-top: 10px" @click="goRecord"
        >></span
      >
      <p>
        <span></span><label>{{ info.shop_name }}</label>
      </p>
      <p>
        <span></span>
        <label>{{ info.remit_account }}</label>
        <span style="font-size: 12px;color:#9c9c9c;margin-left: 10px;"></span>
      </p>
      <p>
        <span></span><label>{{ huanbi_remit }}</label>
        <span></span><label>{{ huanbi_money }}</label>
      </p>
      <p>
        <span></span>
        <span>
          <el-input v-model="withSurplus" size="small" maxlength="10"></el-input>
        </span>
        <label @click="getAll" style="color:#409EFF;font-weight: normal;cursor: pointer;"></label>
      </p>
      <p>
        <span> </span><label>{{ info.bind_user_phone }}</label>
      </p>
      <el-button type="primary" :loading="isLoading" @click="submit" style="width:200px;"></el-button>

      <div class="warn-wrapper">
        <p></p>
        <p>1</p>
        <p>2</p>
      </div>
    </section>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { getSuppliersInfo, huanbiWithdraw, getSuppliersAccount } from '../../api/suppliers'
export default {
  name: 'SettlementInfo',
  data() {
    return {
      info: {},
      huanbi_remit: 0, //
      huanbi_money: 0, //
      withSurplus: '',
      isLoading: false //
    }
  },
  created() {
    this.getWithdrawalInfo()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id
    })
  },
  methods: {
    getWithdrawalInfo() {
      getSuppliersInfo(this.suppliers_id).then(
        res => {
          this.info = res
        },
        error => {
          console.log(error)
        }
      )
      getSuppliersAccount(this.suppliers_id).then(
        res => {
          this.huanbi_money = this.utils.formatFloat(res.huanbi_money, true)
        },
        error => {
          console.log(error)
        }
      )
    },
    getAll() {
      this.withSurplus = this.huanbi_money
    },
    goRecord() {
      this.$router.push({ name: 'WithdrawalRecord' })
    },
    submit() {
      let withSurplus = Number(this.withSurplus)
      if (!withSurplus || withSurplus < 0) {
        this.$alert('')
        return
      }
      this.isLoading = true
      huanbiWithdraw(this.suppliers_id, this.withSurplus)
        .then(
          res => {
            this.$alert(res.info, '', {
              callback: () => {
                this.$router.push({ name: 'WithdrawalRecord' })
              }
            })
          },
          error => {
            this.$alert(error.errorMsg)
          }
        )
        .finally(() => {
          this.isLoading = false
        })
    }
  }
}
</script>

<style lang="scss" scoped>
// f8f8f8
section {
  background-color: #fff;
  padding: 15px 25px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.shop-info {
  p {
    font-size: 14px;
    span {
      display: inline-block;
      width: 150px;
      padding-right: 20px;
    }
    label {
      font-weight: bold;
    }
  }
}
.warn-wrapper {
  margin-top: 30px;
  p {
    font-size: 13px;
    margin: 5px 0;
    color: #9c9c9c;
  }
}
</style>
