import { fetchEndpoint, fetchEndpointBlob } from '../server/network'

// 
export const getRemitInfo = params =>
  fetchEndpoint('/admin/api.remit.settings.get', 'POST', {
    suppliers_id: params
  })

/**
 * @DateTime 2019-07-12
 * @description  []
 * @param    {[string]} pay_company []
 * @param    {[string]} payee []
 * @param    {[string]} remit_account []
 * @param    {[string]} bank []
 * @param    {[float]} money_service_percent []
 * @param    {[float]} huanbi_service_percent []
 * @param    {[float]} money_income_percent []
 * @param    {[float]} huanbi_income_percent []
 * @param    {[float]} money_remit_percent []
 * @param    {[float]} huanbi_remit_percent []
 * @param    {[int]} remit_type [ 1 2]
 */
export const setRemitInfo = params =>
  fetchEndpoint('/admin/api.remit.settings.set', 'POST', {
    ...params
  })

