export const bondReduce = (suppliers_id, bond, direction, remark) =>
  fetchEndpoint('/admin/api.suppliers.bond.reduce', 'POST', {
    suppliers_id: suppliers_id,
    bond: bond,
    direction: direction,
    remark: remark
  })

