<template>
  <div class="container">
    <page-title first-title=""></page-title>
    <div class="button-wrapper">
      <el-button type="primary" @click="activityAdd('add')"></el-button>
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="">
          <el-input v-model="params.partnerName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getActivityList()" icon="el-icon-search"></el-button>
          <el-button type="primary" @click="clear()" icon="el-icon-refresh"></el-button>
        </el-form-item>
        <el-form-item style="float:right;">
          <el-button type="primary">
            <a style="color:#fff;" :href="down" download=".csv"></a>
          </el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table
        :data="onlineArr"
        v-loading="online_loading"
        stripe
        style="width: 100%"
        :header-cell-style="{ background: '#eef1f6' }"
      >
        <el-table-column align="center" type="index" label=""></el-table-column>
        <el-table-column align="center" prop="partnerId" label="ID"></el-table-column>
        <el-table-column align="center" prop="account" label=""></el-table-column>
        <el-table-column align="center" prop="partnerName" label=""></el-table-column>
        <el-table-column align="center" prop="mobile" label=""></el-table-column>
        <el-table-column align="center" prop="useToken" label="">
          <template slot-scope="scope">
            <router-link :to="{ name: 'paymentDetails', params: { partners: scope.row.partnerName } }"
              ><span>{{ scope.row.useToken }}</span></router-link
            >
          </template>
        </el-table-column>
        <el-table-column align="center" prop="token" label=""></el-table-column>
        <el-table-column align="center" prop="lendNum" label="">
          <template slot-scope="scope">
            <router-link :to="{ name: 'lenderLists', params: { partners: scope.row.partnerName } }"
              ><span>{{ scope.row.lendNum }}</span></router-link
            >
          </template>
        </el-table-column>
        <el-table-column align="center" prop="createdAt" label="">
          <template slot-scope="scope">
            <span>{{ utils.format_date(scope.row.createdAt) }}</span>
          </template>
        </el-table-column>
        <el-table-column align="center" label="">
          <template slot-scope="scope">
            <el-button type="text" size="small" class="input-box">
              
              <input type="file" @change="ossUpload($event, scope.row)" />
            </el-button>
            <el-button @click="editTokenClick(scope.row)" type="text" size="small"></el-button>
            <el-button @click="activityAdd('edit', scope.row)" type="text" size="small"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
    <el-dialog
      :title="titleName"
      :visible.sync="addPlatform"
      v-if="addPlatform"
      width="40%"
      v-loading="addPlatform_loading"
      :close-on-click-modal="false"
    >
      <el-form ref="form" :model="add_Platform" label-width="100px" size="mini">
        <el-form-item label="">
          <el-input type="text" v-model.trim.lazy="add_Platform.partnerName" style="width: 60%;"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input
            type="text"
            :disabled="add_Platform.istype == 'edit'"
            v-model.trim.lazy="add_Platform.account"
            style="width: 60%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="text" v-model.trim.lazy="add_Platform.password" style="width: 60%;"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input type="text" v-model.trim.lazy="add_Platform.passwordConfirm" style="width: 60%;"></el-input>
        </el-form-item>
        <!-- <el-form-item label="">
          <el-input type="text" v-model.trim.lazy="add_Platform.title" style="width: 60%;" ></el-input>
        </el-form-item> -->
        <el-form-item label="">
          <el-input
            type="number"
            v-model.trim.lazy="add_Platform.mobile"
            @input="checkTel('phone', $event)"
            style="width: 60%;"
          ></el-input>
        </el-form-item>
        <el-form-item label="" v-if="add_Platform.istype != 'edit'">
          <el-input
            type="number"
            v-model.trim.lazy="add_Platform.token"
            @input="checkTel('token', $event)"
            style="width: 60%;"
          ></el-input>
        </el-form-item>
        <div class="antion-footer">
          <el-button @click="addPlatform = false"> </el-button>
          <el-button type="primary" @click="submit" :loading="addBtn"> </el-button>
        </div>
      </el-form>
    </el-dialog>
    <el-dialog title="" :visible.sync="kidneyBean" v-if="kidneyBean" width="30%" :close-on-click-modal="false">
      <el-form ref="form" :model="editToken" label-width="100px" size="mini">
        <el-form-item label="">
          <el-input
            type="number"
            v-model.trim.lazy="editToken.token"
            placeholder=""
            maxlength="8"
            @input="checkTel('token', $event)"
            style="width: 100%;"
          ></el-input>
        </el-form-item>
        <div class="antion-footer">
          <el-button @click="kidneyBean = false"> </el-button>
          <el-button type="primary" @click="increaseOrDecrease" :loading="editBtn"> </el-button>
        </div>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import ROOT from '../../../api/root.js'
export default {
  name: 'RetailActivityList',
  data() {
    return {
      onlineArr: [],
      currentPage: 1,
      params: {
        partnerName: '', //
        page: 1,
        limit: 10
      },
      add_Platform: {
        partnerName: '', //
        token: '', //
        account: '', //
        password: '', //
        passwordConfirm: '', //
        mobile: '', //
        partnerId: '' //ID
      },
      total: 0,
      addPlatform_loading: false,
      addPlatform: false,
      kidneyBean: false,
      addBtn: false,
      editBtn: false,
      editToken: {
        token: '',
        partnerId: ''
      },
      fileList3: [],
      titleName: '',
      online_loading: false,
      down: ''
    }
  },
  created() {
    this.getActivityList()
    for (let i in ROOT) {
      if (ROOT[i].key == 'hd/') {
        this.down = ROOT[i].val + '/template(UTF-8).csv'
      }
    }
  },
  methods: {
    checkTel(type, e) {
      if (type == 'phone') {
        if (this.add_Platform.mobile.length > 11) {
          this.add_Platform.mobile = this.add_Platform.mobile.slice(0, 11)
        }
      } else if (type == 'token') {
        if (e == 0) {
          this.editToken.token = ''
        }
        if (this.editToken.token.length > 8) {
          this.editToken.token = this.editToken.token.slice(0, 8)
        }
      }
    },
    replaceDatas(data) {
      for (let key in data) {
        if (data[key] == '' || data[key] == null) {
          delete data[key]
        }
      }
    },
    getActivityList() {
      this.replaceDatas(this.params)
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get('hd/itzPartner/page/list?' + pramstr, null, res => {
        if (res.status === 200) {
          this.onlineArr = res.data.rows
          this.$set(this.onlineArr, res.data.rows)
          this.total = res.data.total
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getActivityList()
    },
    /**
     *  private String partnerName;
     *  private BigDecimal token;
     *  private Integer createdAt;
     *  private Integer updatedAt;
     *  private String account;
     *  private String password;
     *  private String passwordConfirm;
     *  private String mobile;
     */
    activityAdd(type, n) {
      console.log(n)
      if (type == 'add') {
        this.add_Platform = {
          istype: 'add',
          partnerName: '', //
          token: '', //
          account: '', //
          password: '', //
          passwordConfirm: '', //
          mobile: '', //
          partnerId: '' //ID
        }
        this.titleName = ''
      } else if (type == 'edit') {
        this.add_Platform = {
          istype: 'edit',
          partnerName: n.partnerName, //
          token: n.token, //
          account: n.account, //
          password: '', //
          passwordConfirm: '', //
          mobile: n.mobile, //
          partnerId: n.partnerId //ID
        }
        this.titleName = ''
      }
      this.addPlatform = true
    },
    // 
    clear() {
      // console.log(Object.values(this.params));
      this.page = 1
      this.limit = 10
      let _params = this.params
      _params.partnerName = ''
      this.getActivityList()
    },
    /**
     * 
     */
    ossUpload(e, n) {
      console.log(e.target.files, n)
      let that = this,
        files = e.target.files
      for (let i of files) {
        if (i.name && !i.name.indexOf('.csv') == -1) {
          that.$message.error('.CSV ')
          continue
        }
      }
      that.online_loading = true
      let formData = new FormData()
      formData.append('file', files[0])
      that.$api.post('hd/upload/' + n.partnerId, formData, res => {
        console.log(res)
        if (res.status === 200) {
          that.online_loading = false
          that.$message.success('')
          this.getActivityList()
        }
      })
    },
    editTokenClick(e) {
      console.log(e)
      this.kidneyBean = true
      this.editToken = {
        token: '',
        partnerId: e.partnerId
      }
    },
    /**
     * 
     * @param {obj} pramstr
     * token:'',
     * partnerId:'' 
     */
    increaseOrDecrease() {
      this.editBtn = true
      let pramstr = this.editToken
      if (!this.editToken.token) {
        this.$message.error(``)
        return false
      }
      if (this.editToken.token && this.editToken.token.length > 8) {
        this.$message.error(``)
        return false
      }
      this.$api.put('hd/itzPartner/huandou', pramstr, res => {
        console.log(res)
        if (res.status === 200) {
          this.$message.success(``)
          this.editBtn = false
          this.kidneyBean = false
          this.getActivityList()
        }
      })
    },
    /**
     * 
     * @param {obj} add_Platform
     * add_Platform.partnerName:'', //
     * add_Platform.token:'',//
     * add_Platform.account :'', //
     * add_Platform.password :'', //
     * add_Platform.passwordConfirm:'', //
     * add_Platform.mobile:'', //
     * add_Platform.partnerId:''//ID
     */
    submit() {
      if (this.add_Platform.istype == 'add') {
        if (!this.add_Platform.partnerName) {
          this.$message.error(``)
          return false
        }
        if (!this.add_Platform.account) {
          this.$message.error(``)
          return false
        }
        if (!this.add_Platform.account) {
          this.$message.error(``)
          return false
        }
        if (!this.add_Platform.password) {
          this.$message.error(``)
          return false
        }
        if (this.add_Platform.password != this.add_Platform.passwordConfirm) {
          this.$message.error(``)
          return false
        }
        let telrule = /^1[3456789]\d{9}$/
        if (!this.add_Platform.mobile) {
          this.$message.error(``)
          return false
        }
        if (!telrule.test(this.add_Platform.mobile)) {
          this.$message.error(``)
          return false
        }
        if (!this.add_Platform.token) {
          this.$message.error(``)
          return false
        }
        if (this.add_Platform.token && this.add_Platform.token.length > 8) {
          this.$message.error(``)
          return false
        }
        let pramstr = this.add_Platform
        this.addBtn = true
        this.$api.post('hd/itzPartner/info', pramstr, res => {
          console.log(res)
          if (res.status === 200) {
            this.$message.success(``)
            this.addBtn = false
            this.addPlatform = false
            this.getActivityList()
          }
        })
      } else if (this.add_Platform.istype == 'edit') {
        this.addBtn = true
        let pramstr = this.add_Platform
        this.$api.put('hd/itzPartner/info', pramstr, res => {
          if (res.status === 200) {
            this.$message.success(``)
            this.addBtn = false
            this.addPlatform = false
            this.getActivityList()
          }
        })
      }
    }
  },
  watch: {
    addPlatform(val) {
      let that = this
      if (val === false || val === 'false') {
        console.log('add_Platform')
        that.add_Platform = {
          istype: '',
          partnerName: '', //
          token: '', //
          account: '', //
          password: '', //
          passwordConfirm: '', //
          mobile: '', //
          partnerId: '' //ID
        }
        that.addBtn = false
      }
    },
    kidneyBean(val) {
      let that = this
      if (val === false || val === 'false') {
        console.log('')
        that.editToken = {
          token: '',
          partnerId: ''
        }
        that.editBtn = false
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  // text-align: right;
  padding: 15px 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.antion-footer {
  text-align: center;
  margin: 30px 0 0;
}
.el-input--mini .el-input__inner {
  height: 32px;
  line-height: 32px;
}
.input-box {
  position: relative;
}
.input-box input {
  opacity: 0;
  position: absolute;
  left: 0;
  bottom: 0;
  width: 50px;
}
</style>
