<template>
  <div class="container">
    <page-title first-title=""></page-title>
    <div class="button-wrapper">
      <!-- <el-button type="primary" @click="activityAdd()"></el-button> -->
    </div>
    <div class="search-wrapper">
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="">
          <el-select v-model="params.changeType">
            <el-option v-for="item in options" :key="item.value" :label="item.value" :value="item.label"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="params.orderId" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="params.partnerName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="params.nameOrMobile" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="getpaymentDetails()" icon="el-icon-search"></el-button>
          <el-button type="primary" @click="clear()" icon="el-icon-refresh"></el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="list-wrapper">
      <el-table :data="paymentArr" stripe style="width: 100%" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column prop="orderId" label=""></el-table-column>
        <el-table-column prop="changeToken" label=""></el-table-column>
        <el-table-column prop="createdAt" label="">
          <template slot-scope="scope">
            <span>{{ utils.format_date(scope.row.createdAt) }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="userName" label=""></el-table-column>
        <el-table-column prop="mobilePhone" label=""></el-table-column>
        <el-table-column prop="partnerName" label=""></el-table-column>
      </el-table>
    </div>
    <!--  -->
    <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size="params.limit"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: 'RetailActivityList',
  data() {
    return {
      paymentArr: [],
      currentPage: 1,
      params: {
        page: 1,
        limit: 10,
        changeType: '', //-10:() 1: 2: 3:
        orderId: '', //
        partnerName: '', //
        nameOrMobile: '' //
        // partnerId:'1'
      },
      total: 0,
      addPlatform_loading: false,
      addPlatform: false,
      options: [
        {
          value: '()',
          label: '0'
        },
        {
          value: '',
          label: '1'
        },
        {
          value: '',
          label: '2'
        },
        {
          value: '',
          label: '3'
        }
      ]
    }
  },
  created() {
    console.log(this.$route)
    this.params.partnerName = this.$route.params.partners
    this.getpaymentDetails()
  },
  methods: {
    /**
     *  -1  0:() 1: 2: 3:
     * private Integer changeType = -1;
     *  private String pipelineNo;
     *  private Integer orderId;
     *  private String partnerName;
     *   private String nameOrMobile;
     * id private Integer partnerId = -1;
     * private Integer page = 1;
     * private Integer limit = 10;
     */
    getpaymentDetails() {
      this.$api.post('hd/itzUserTokenLog/prepare_detail', this.params, res => {
        if (res.status == 200) {
          this.paymentArr = res.data.rows
          this.total = res.data.total
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.page = currentPage
      this.getpaymentDetails()
    },
    // 
    clear() {
      // console.log(Object.values(this.params));
      this.params.page = 1
      this.params.limit = 10
      this.params = {
        page: 1,
        limit: 10,
        changeType: '', //-10:() 1: 2: 3:
        orderId: '', //
        partnerName: '', //
        nameOrMobile: '', //
        partnerId: '1'
      }
      this.getpaymentDetails()
    }
  },
  watch: {
    addPlatform(val) {
      if (val === false || val === 'false') {
        console.log('add_Platform')
      }
    },
    $route: {
      handler(val) {
        let that = this
        console.log(val, '')
        if (val.name === 'paymentDetails') {
          //
          that.params.partnerName = val.params.partner ? val.params.partner : ''
        }
      },
      deep: true //
    }
  }
}
</script>

<style lang="scss" scoped>
.button-wrapper {
  // text-align: right;
  padding: 15px 20px;
}
.el-pagination {
  text-align: center;
  margin-top: 10px;
}
.antion-footer {
  text-align: center;
  margin: 30px 0 0;
}
.el-input--mini .el-input__inner {
  height: 32px;
  line-height: 32px;
}
</style>
