<template>
  <div class="container">
    <page-title first-title="" second-title=""></page-title>

    <section>
      <!--  -->
      <template v-if="!isEdit">
        <el-button type="primary" @click="editInfo" style="float: right;"></el-button>
        <div class="info-item">
          <p class="title"></p>
          <p class="item-line">
            <span></span>
            <label>{{ info.pay_company }}</label>
          </p>
          <p class="item-line">
            <span></span>
            <label>{{ info.payee }}</label>
          </p>
        </div>
        <div class="info-item" v-if="info.cooperate_type == COOPERATE_TYPE.COR_RESIDENT">
          <p class="title"></p>
          <p class="item-line">
            <label>{{ HUANBI_SETTLEMENT_TXT[info.is_huanbi_settlement] }}</label>
          </p>
        </div>
        <div class="info-item">
          <p class="title"></p>
          <template v-if="info.remit_type == PAYTYPE.BANK">
            <p class="item-line">
              <span></span>
              <label>{{ info.open_bank }}</label>
            </p>
            <p class="item-line">
              <span></span>
              <label>{{ bankName }}</label>
            </p>
            <p class="item-line">
              <span></span>
              <label>{{ info.remit_account }}</label>
            </p>
          </template>
          <template v-else>
            <p class="item-line">
              <span></span>
              <label>{{ info.remit_account }}</label>
            </p>
          </template>
        </div>
        <div class="info-item">
          <p class="title"></p>
          <template v-if="info.commission_type === COMMISSIONTYPE.SLAES">
            <p class="item-line">
              <span></span>
              <label>{{ info.sales_service_percent }}%</label>
            </p>
          </template>
          <template v-else>
            <p class="item-line">
              <span></span>
              <label>{{ info.cash_service_percent }}%</label>
              <span></span>
              <label>{{ info.huanbi_service_percent }}%</label>
            </p>
            <p class="item-line">
              <span></span>
              <label>{{ info.cash_income_percent }}%</label>
              <span></span>
              <label>{{ info.huanbi_income_percent }}%</label>
            </p>
            <p class="item-line">
              <span></span>
              <label>{{ info.cash_remit_percent }}%</label>
              <span></span>
              <label>{{ info.huanbi_remit_percent }}%</label>
            </p>
          </template>
        </div>
        <div class="info-item">
          <p class="title"></p>
          <p class="item-line">
            <span></span>
            <label>{{ info.create_multi_method == 1 ? '' : '' }}</label>
          </p>
        </div>
        <div class="info-item" v-if="info.create_multi_method == 2">
          <p class="title"></p>
          <p class="item-line">
            <span></span>
            <label>{{ info.bond_amount }}</label>
          </p>
        </div>
      </template>
      <!--  -->
      <template v-else>
        <div class="settle-setting-wrapper">
          <el-form
            :model="info_edit"
            :rules="rules"
            ref="infoForm"
            :validate-on-rule-change="ruleFix"
            label-width="150px"
          >
            <!-- <el-button type="primary" @click="save" style="float: right;"></el-button>
              <el-button @click="cancel" style="float: right;margin-right: 20px;"></el-button> -->
            <div class="info-item">
              <p class="title"></p>
              <el-form-item label="" prop="pay_company">
                <el-input v-model="info_edit.pay_company" size="small"></el-input>
              </el-form-item>
              <el-form-item label="" prop="payee">
                <el-input v-model="info_edit.payee" size="small"></el-input>
              </el-form-item>
            </div>
            <div class="info-item" v-if="info.cooperate_type == COOPERATE_TYPE.COR_RESIDENT">
              <el-form-item label="" prop="remit_type">
                <el-switch
                  v-model="info_edit.is_huanbi_settlement"
                  :active-value="HUANBI_SETTLEMENT.SETTLEMENT"
                  :inactive-value="HUANBI_SETTLEMENT.UNSETTLEMENT"
                ></el-switch>
              </el-form-item>
            </div>
            <div class="info-item">
              <p class="title"></p>

              <div class="pay-wrapper">
                <el-form-item label="" prop="remit_type">
                  <el-switch
                    v-model="info_edit.remit_type"
                    :active-value="PAYTYPE.ALIPAY"
                    :inactive-value="PAYTYPE.BANK"
                  ></el-switch>
                </el-form-item>
                <template v-if="info_edit.remit_type == PAYTYPE.ALIPAY">
                  <el-form-item label="" prop="remit_account">
                    <el-input v-model="info_edit.remit_account" size="small"></el-input>
                  </el-form-item>
                </template>
              </div>
              <div class="pay-wrapper">
                <el-form-item label="" prop="remit_type">
                  <el-switch
                    v-model="info_edit.remit_type"
                    :active-value="PAYTYPE.BANK"
                    :inactive-value="PAYTYPE.ALIPAY"
                  ></el-switch>
                </el-form-item>
                <template v-if="info_edit.remit_type == PAYTYPE.BANK">
                  <el-form-item label="" prop="open_bank">
                    <el-input v-model="info_edit.open_bank" size="small"></el-input>
                  </el-form-item>
                  <el-form-item label="" prop="bank">
                    <el-select v-model="info_edit.bank" filterable>
                      <el-option
                        v-for="item in bank_list"
                        :key="item.short"
                        :label="item.name"
                        :value="item.short"
                      ></el-option>
                    </el-select>
                    <span style="color: #8888;padding-left: 20px"></span>
                  </el-form-item>
                  <el-form-item label="" prop="remit_account">
                    <el-input v-model="info_edit.remit_account" size="small"></el-input>
                  </el-form-item>
                </template>
              </div>
            </div>
            <div class="info-item">
              <p class="title"></p>
              <el-form-item prop="commission_type">
                <el-radio v-model="info_edit.commission_type" :label="COMMISSIONTYPE.SLAES"></el-radio>
                <el-radio v-model="info_edit.commission_type" :label="COMMISSIONTYPE.CASHHUAN"></el-radio>
              </el-form-item>

              <template v-if="info_edit.commission_type === COMMISSIONTYPE.SLAES">
                <el-form-item label="" prop="cash_service_percent">
                  <el-input v-model="info_edit.sales_service_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
              </template>
              <template v-else>
                <el-form-item label="" prop="cash_service_percent">
                  <el-input v-model="info_edit.cash_service_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
                <el-form-item label="" prop="huanbi_service_percent">
                  <el-input v-model="info_edit.huanbi_service_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
                <el-form-item label="" prop="cash_income_percent">
                  <el-input v-model="info_edit.cash_income_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
                <el-form-item label="" prop="huanbi_income_percent">
                  <el-input v-model="info_edit.huanbi_income_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
                <el-form-item label="" prop="cash_remit_percent">
                  <el-input v-model="info_edit.cash_remit_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
                <el-form-item label="" prop="huanbi_remit_percent">
                  <el-input v-model="info_edit.huanbi_remit_percent" size="small" style="width: 150px"></el-input>%
                </el-form-item>
              </template>
            </div>
            <div class="info-item">
              <p class="title"></p>
              <div class="account-statement">
                <p>
                  <el-radio v-model="info_edit.create_multi_method" :label="2"></el-radio>
                  <span class="tip">0</span>
                </p>
                <p>
                  <el-radio v-model="info_edit.create_multi_method" :label="1"></el-radio>
                  <span class="tip"></span>
                </p>
              </div>
              <div class="bond-limit" v-if="info_edit.create_multi_method == 2">
                <p class="title"></p>
                <span class="required-tip">*</span>
                <el-input size="small" v-model="info_edit.bond_amount" clearable></el-input>&nbsp;&nbsp;
              </div>
            </div>
            <div class="btn-box" style="text-align: center;">
              <el-button type="primary" @click="save"></el-button>
              <el-button @click="cancel"></el-button>
            </div>
          </el-form>
        </div>
      </template>
    </section>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import { getRemitInfo, setRemitInfo } from '../../api/settle'
import { PAYTYPE, COMMISSIONTYPE, COOPERATE_TYPE, HUANBI_SETTLEMENT_TXT, HUANBI_SETTLEMENT } from './static'
export default {
  name: 'SettleSetting',
  data() {
    return {
      isEdit: false, //
      ruleFix: true,
      PAYTYPE: PAYTYPE,
      COMMISSIONTYPE: COMMISSIONTYPE,
      COOPERATE_TYPE: COOPERATE_TYPE,
      HUANBI_SETTLEMENT_TXT: HUANBI_SETTLEMENT_TXT,
      HUANBI_SETTLEMENT: HUANBI_SETTLEMENT,
      info: {},
      info_edit: {},
      bank_list: [],
      rules: {
        pay_company: [{ required: true, message: '', trigger: 'blur' }],
        payee: [{ required: true, message: '', trigger: 'blur' }],
        remit_account: [{ required: true, message: '', trigger: 'blur' }],
        open_bank: [{ required: true, message: '', trigger: 'blur' }],
        bank: [{ required: true, message: '', trigger: 'blur' }],

        cash_service_percent: [{ required: true, message: '', trigger: 'blur' }],
        huanbi_service_percent: [{ required: true, message: '', trigger: 'blur' }],
        cash_income_percent: [{ required: true, message: '', trigger: 'blur' }],
        huanbi_income_percent: [{ required: true, message: '', trigger: 'blur' }],
        cash_remit_percent: [{ required: true, message: '', trigger: 'blur' }],
        huanbi_remit_percent: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  created() {
    this.getInfo()
  },
  computed: {
    ...mapState({
      suppliers_id: state => state.suppliers.suppliers_id
    }),
    bankName() {
      let bk_name = ''
      if (this.info.bank && this.bank_list.length) {
        let bankItem = this.bank_list.filter(val => {
          return val.short === this.info.bank
        })
        bk_name = bankItem[0].name
      }
      return bk_name
    }
  },
  methods: {
    ...mapMutations({
      saveBankList: 'saveBankList'
    }),
    getInfo() {
      getRemitInfo(this.suppliers_id).then(
        res => {
          this.info = res
          this.bank_list = res.bank_list
          this.saveBankList(res.bank_list)
        },
        error => {
          console.log(error)
        }
      )
    },
    editInfo() {
      this.info_edit = { ...this.info }
      this.isEdit = true
    },
    cancel() {
      this.isEdit = false
      this.info_edit = {}
    },
    save() {
      this.$refs['infoForm'].validate(validate => {
        if (validate) {
          this.submit()
        } else {
          return false
        }
      })
    },
    submit() {
      let data = {}
      data['suppliers_id'] = this.suppliers_id
      data['pay_company'] = this.info_edit.pay_company
      data['payee'] = this.info_edit.payee
      data['remit_account'] = this.info_edit.remit_account
      data['bank'] = this.info_edit.bank
      data['open_bank'] = this.info_edit.open_bank
      data['remit_type'] = this.info_edit.remit_type
      data['commission_type'] = this.info_edit.commission_type
      data['is_huanbi_settlement'] = this.info_edit.is_huanbi_settlement
      if (this.info_edit.commission_type === COMMISSIONTYPE.SLAES) {
        data['cash_service_percent'] = data['huanbi_service_percent'] = data['cash_income_percent'] = data[
          'huanbi_income_percent'
        ] = data['cash_remit_percent'] = data['huanbi_remit_percent'] = 0
        data['sales_service_percent'] = this.info_edit.sales_service_percent
      } else {
        data['cash_service_percent'] = this.info_edit.cash_service_percent
        data['huanbi_service_percent'] = this.info_edit.huanbi_service_percent
        data['cash_income_percent'] = this.info_edit.cash_income_percent
        data['huanbi_income_percent'] = this.info_edit.huanbi_income_percent
        data['cash_remit_percent'] = this.info_edit.cash_remit_percent
        data['huanbi_remit_percent'] = this.info_edit.huanbi_remit_percent
        data['sales_service_percent'] = 0
      }
      data['create_multi_method'] = this.info_edit.create_multi_method
      data['bond_amount'] = this.info_edit.bond_amount
      if (this.info_edit.create_multi_method == 2) {
        if (!this.info_edit.bond_amount) {
          this.$message.error('')
          return
        }
      }
      const loading = this.$loading({
        lock: true,
        text: '',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      })
      setRemitInfo(data)
        .then(
          res => {
            console.log(res)
            this.isEdit = false
          },
          error => {
            console.log(error)
          }
        )
        .finally(() => {
          this.getInfo()
          loading.close()
        })
    }
  }
}
</script>

<style lang="scss" scoped>
section {
  background-color: #fff;
  padding: 15px 20px;
  margin-top: 16px;
  h2 {
    margin: 5px 5px;
  }
}
.header {
  margin-bottom: 10px;
}
.info-item {
  margin-bottom: 30px;
  // &:nth-last-child(1) {
  //   margin: 0;
  // }
  /* .title {
    font-size: 1.3em;
    color: #9c9c9c;
  } */
  .item-line {
    span,
    label {
      font-size: 1.3em;
      display: inline-block;
    }
    span {
      width: 150px;
    }
    label {
      width: 250px;
      font-weight: bold;
    }
  }
}
.title {
  font-size: 1.3em;
  color: #9c9c9c;
}
.pay-wrapper {
  margin-bottom: 20px;
}
.el-input {
  width: 250px;
}
.settle-setting-wrapper {
  padding-left: 30px;
  .bond-limit {
    margin-top: 50px;
  }
  .tip {
    color: #9c9c9c;
  }
  .required-tip {
    color: red;
    margin-right: 5px;
  }
}
</style>
